import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useDimension } from 'Page/StockEntry/GeneralServices/'
import CustomTable from 'Component/CustomTable'
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";
import { getSyncHistories, syncAllHistories } from 'apiServices'
import { activeStockTableHeader, data } from './services'
import Saved from './Saved'
import PopUp from "Component/Modal/PopUp";

const { height, emptyItem, width, isMobile } = useDimension(200)
const tableHeight = height + 50
const customTableClass = isMobile ? ('stockMovement' === 'stockMovement' ? 'target-movement-mobile' : 'target-repack-mobile') : ('stockMovement' === 'stockMovement' ? 'target-movement' : 'target-repack')

const ActiveStock = (props) => {
    const dispatch = useDispatch()
    const status = useSelector((state) => state.status);
    const syncHistories = useSelector(state => state.syncHistories)
    const userName = useSelector(state => state.user.email)
    const thereAreRequest = useSelector(state => state.thereAreRequest);
    const warehouseUuid = useSelector((state) => state.user.warehouseUuid);
    // const showSubmit = process.env.REACT_APP_API_URL === 'https://inventory-management-oss-prd.apps.aws.sayurbox.io/' ? false : true
    const showSubmit = true
    const [submit, setSubmit] = useState()
    useEffect(_ => {
        getSyncHistories({ dispatch, warehouseUuid })
    }, [])

    if (thereAreRequest !== 0 && !syncHistories?.length) return (
        <div className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center" style={{ minHeight: emptyItem + 200, maxHeight: emptyItem + 200 }}>
            <img className="loading" src={loading} alt="loading" />
        </div>
    )
    return (
        <div className="bg-white border radius-all w-100 active-stock" style={{ minHeight: height, height: height, maxHeight: height }} >
            <Saved isSaved={true} submit={submit} setSubmit={(value) => setSubmit(value)} thereAreRequest={thereAreRequest} userName={userName} status={status} />
            {/* <PopUp status={status} /> */}
            <div style={{ minHeight: tableHeight }}>
                <CustomTable
                    onClick={(v) => props.history.push(`/settings/active-stock/${v?.id}`)}
                    editColumn={'false'}
                    className={`${customTableClass} pointer`}
                    styles={{ color: 'red', textAlign: 'right', width: '100%', maxHeight: tableHeight }}
                    data={syncHistories?.length ? syncHistories : []}
                    columns={activeStockTableHeader} />
            </div>

            <div className='w-100 d-flex justify-content-end'>
                <button onClick={() => syncAllHistories({ warehouseUuid, setSubmit })} type="button" className={`${showSubmit ? null : 'd-none'} btn px-5 mt-3 sku-item no-shadow text-white ${!thereAreRequest && syncHistories?.length ? "bg-green" : "bg-lineGrey"}`}>{thereAreRequest ? 'Processing...' : 'Update Stock Active'}</button>
            </div>
        </div >
    )
}

export default ActiveStock